.mobile-nav {
  width: 100%;
  max-width: 300px;
  height: 100vh;
  transition: all 0.3s ease;
  background: #FFF;
  position: fixed;
  left: 0;
  top: 0;
  box-shadow: none;
  overflow-y: auto;
  z-index: 100;
  display: none;
  transform: translateX(-100%);
  transition: transform 0.3s ease, box-shadow 0.2s ease 0.4s;
}
.mobile-nav--show {
  transform: translateX(0);
  box-shadow: 0 0 0 3000px rgba(0, 0, 0, 0.5);
}
@media (max-width: 767px) {
  .mobile-nav {
    display: block;
  }
}
.mobile-nav__inner {
  width: 100%;
  position: relative;
  padding: 45px 20px 30px;
}
.mobile-nav__close {
  width: 40px;
  height: 40px;
  position: absolute;
  right: 10px;
  top: 15px;
}
.mobile-nav__close-ico {
  width: 100%;
  height: 100%;
  fill: #a993dc;
}
.mobile-nav__nav--menu {
  margin-top: 20px;
  padding-top: 20px;
  border-top: 2px solid #f3f3f3;
}
.mobile-nav__nav-item {
  margin-bottom: 20px;
}
.mobile-nav__nav-item:last-child {
  margin-bottom: 0;
}
.mobile-nav__nav-item--opened .mobile-nav__nav-ico {
  transform: rotate(180deg);
}
.mobile-nav__nav-item--opened .mobile-nav__nav-dropdown {
  margin-top: 15px;
  overflow: auto;
}
.mobile-nav__nav-item--page .mobile-nav__nav-ico {
  color: #a2a1a1;
  transform: rotate(-q90deg);
}
.mobile-nav__nav-item--page .mobile-nav__nav-link {
  font-size: 15px;
  color: #a2a1a1;
  font-weight: 300;
}
.mobile-nav__nav-btn {
  width: 20px;
  height: 20px;
  left: 0;
  top: 0;
  position: absolute;
  cursor: pointer;
  z-index: 3;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
.mobile-nav__nav-btn--disabled {
  pointer-events: none;
}
.mobile-nav__nav-btn--disabled .mobile-nav__nav-ico {
