  display: block;
  padding-top: 100%;
}
.product__slider-thumb::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #7dca3f;
  opacity: 0;
  pointer-events: none;
  z-index: 1;
}
